import React, { Component } from 'react';
import {CInput} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import Select from 'react-select';
import { Logger } from 'src/Utils/Utils';

//default
const FILE_NAME = "ItemInputForm.js";

//default
const initialState = {
    isLoaded: false
};

class ItemInputForm extends Component { //dynamic input form for `item-data`.

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    componentDidMount() {
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            <div style={{
                borderStyle:"solid",
                borderColor:"red",
                padding: "10px 10px 10px 10px",
                margin: "0px 0px 10px 0px"
            }}>
                <h5><CIcon size='lg' name="cilMoney" />&nbsp;&nbsp;Item #{this.props.itemIndex + 1} :</h5>

                <Select 
                    onChange={(choice) => {
                        this.props.onProductNameSelection(choice, this.props.itemIndex);
                    }}
                    options={this.props.productListDataSelectTagSpecific}
                    placeholder='Product name'
                    isClearable={true}
                />
                <CInput
                    placeholder="Quantity" 
                    autoComplete="on"
                    type="number"
                    step="0.01"
                    id={"inputItemQuantity_" + this.props.itemIndex}
                    label="Quantity"
                    required
                />
                <CInput
                    placeholder="Amount" 
                    autoComplete="on"
                    type="number"
                    step="0.01"
                    id={"inputItemAmount_" + this.props.itemIndex}
                    label="Amount"
                    required
                />
            </div>
        )
    }

}

export default ItemInputForm;
